import Select from 'react-select';
import {
  container,
  valueContainer,
  input,
  control,
  indicatorSeparator,
  dropdownIndicator,
  menu,
  menuList,
  option,
} from './TableSelect.styled';

const optionsMonth = [
  { value: 1, label: 'January' },
  { value: 2, label: 'February' },
  { value: 3, label: 'March' },
  { value: 4, label: 'April' },
  { value: 5, label: 'May' },
  { value: 6, label: 'June' },
  { value: 7, label: 'July' },
  { value: 8, label: 'August' },
  { value: 9, label: 'September' },
  { value: 10, label: 'October' },
  { value: 11, label: 'November' },
  { value: 12, label: 'December' },
];

const TableSelectMonth = ({ onSelect }) => {
  return (
    <Select
      options={optionsMonth}
      defaultValue={optionsMonth[0]}
      onChange={onSelect}
      styles={{
        container,
        valueContainer,
        input,
        control,
        indicatorSeparator,
        dropdownIndicator,
        menu,
        menuList,
        option,
      }}
    />
  );
};

export default TableSelectMonth;
